import { useState } from "react";

const Textform = (props) => {
  const [text, setText] = useState(" ");

  const toUpper = () => {
    let n = text.toUpperCase();
    setText(n);
  };
  const toLower = () => {
    let n = text.toLowerCase();
    setText(n);
  };

  const ClearText = () => {
    let n = "";
    setText(n);
  };

  const [myStyle, setmyStyle] = useState({
    color: "black",
    backgroundColor: "white"
  });

  const [btnText, setbtnText] = useState("Dark Mode");

  function Mode() {
    if (myStyle.color === "black") {
      setmyStyle({
        color: "white",
        backgroundColor: "black"
      });

      setbtnText("Normal Mode");
    } else if (myStyle.color === "white") {
      setmyStyle({
        color: "black",
        backgroundColor: "white"
      });
      setbtnText("Dark Mode");
    }
  }

  const onChangefun = (event) => {
    setText(event.target.value);
  };

  return (
    <>
      <div style={myStyle}>
        <h1>{props.heading}</h1>
        <div className="mb-3">
          <textarea
            onChange={onChangefun}
            className="form-control"
            id="myBox"
            rows="3"
            value={text}
          ></textarea>
        </div>
        <button onClick={toUpper} className="btn btn-primary mx-3">
          convert to uppercase
        </button>
        <button onClick={toLower} className="btn btn-secondary my-3">
          convert to lowercase
        </button>
        <button onClick={ClearText} className="btn btn-secondary mx-3 my-3">
          Clear The Text
        </button>

        <button onClick={Mode} className="btn btn-secondary mx-3 my-3">
          {btnText}
        </button>
      </div>
      <div className="container">
        <p>
          {text.split(" ").length} words {text.length} characters
        </p>
      </div>
    </>
  );
};

export default Textform;
